$(".filter-item__show").click(function () {
    $(".filter-item").removeClass("_active")
    $(this).parents(".filter-item").toggleClass("_active")
})

$(document).mouseup(function(e) {
    var div = $('.filter-item');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(div).hasClass("_active")){
            $(div).removeClass("_active")
        }
    }
});
