function mainSlider() {
    var swiper = new Swiper('.main-slider .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 10,
        loop: true,
        navigation: {
            nextEl: '.main-slider .swiper-button-next',
            prevEl: '.main-slider .swiper-button-prev',
        },
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        spaceBetween: 0,
        slidesPerView: "auto",
        freeMode: true,
        watchSlidesProgress: true,
        breakpoints: {
            320: {
                direction: "horizontal"
            },
            576: {
                direction: "vertical"
            },
        }
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 10,
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: '.gallery .swiper-button-next',
            prevEl: '.gallery .swiper-button-prev',
        },
        breakpoints: {
            320: {
                direction: "horizontal"
            },
            576: {
                direction: "vertical"
            },
        }
    });
}

function productSlider() {
    var swiper = new Swiper('.product-slider .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        navigation: {
            nextEl: '.product-slider .swiper-button-next',
            prevEl: '.product-slider .swiper-button-prev',
        },
    })
}

function gallery2() {
    var swiper = new Swiper(".gallery2 .mySwiper", {
        spaceBetween: 0,
        slidesPerView: "auto",
        freeMode: true,
        watchSlidesProgress: true,
    });
    var swiper2 = new Swiper(".gallery2 .mySwiper2", {
        spaceBetween: 10,
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: '.gallery2 .swiper-button-next',
            prevEl: '.gallery2 .swiper-button-prev',
        },
    });
}

mainSlider()
gallery()
productSlider()
gallery2()
